import * as d3 from "d3";

const ThornicroftsGiraffe = () => {
	const height = 800;
	const width = 800;

	const svg = d3.create("svg");

	const xpart = height / 6;
	const ypart = width / 5;
	let circles = [];
	for (let i = 0; i < 6; i++) {
		for (let j = 0; j < 5; j++) {
			circles.push({
				x:
					Math.random() *
						((i + 1) * xpart * 0.95 - i * xpart * 1.15) +
					i * xpart * 1.1,
				y:
					Math.random() *
						((j + 1) * ypart * 0.95 - j * ypart * 1.15) +
					j * ypart * 1.1,
			});
		}
	}

	let voronoi = d3.Delaunay.from(
		circles,
		(d) => d.x,
		(d) => d.y
	).voronoi([0, 0, width, height]);

	const shades = ["#8b5f41"];

	let defs = svg.append("defs");

	for (let i = 0; i < circles.length; i++) {
		defs.append("linearGradient")
			.attr("id", i)
			.selectAll("stop")
			.data([
				{
					offset: "0%",
					color: shades[Math.floor(Math.random() * shades.length)],
				},
				{
					offset: "30%",
					color: shades[Math.floor(Math.random() * shades.length)],
				},
				{
					offset: "60%",
					color: shades[Math.floor(Math.random() * shades.length)],
				},
				{
					offset: "100%",
					color: shades[Math.floor(Math.random() * shades.length)],
				},
			])
			.enter()
			.append("stop")
			.attr("offset", function (d) {
				return d.offset;
			})
			.attr("stop-color", function (d) {
				return d.color;
			});

		defs.append("clipPath")
			.attr("id", `clipPath${i}`)
			.append("path")
			.attr("d", voronoi.renderCell(i));

		svg.append("circle")
			.attr("r", 200)
			.attr("cx", circles[i].x)
			.attr("cy", circles[i].y)
			.attr("clip-path", `url(#clipPath${i})`)
			.attr("spreadMethod", "reflect")
			.style("fill", `url('#${i}')`);
	}

	function dupa(i) {
		const NUM_POINTS = 80;
		const path = document.createElementNS(
			"http://www.w3.org/2000/svg",
			"path"
		);
		const d = voronoi.renderCell(i);
		path.setAttribute("d", d);
		const len = path.getTotalLength();
		let points = [];

		for (let j = 0; j < NUM_POINTS; j++) {
			let pt = path.getPointAtLength((j * len) / (NUM_POINTS - 1));

			if (Math.random() < 0.5 / 100) {
				let pt2 = path.getPointAtLength((j * len) / (NUM_POINTS - 5));
				if (circles[i] != undefined) {
					svg.append("polygon")
						.attr("points", [
							pt.x,
							pt.y,
							pt2.x,
							pt2.y,
							circles[i].x,
							circles[i].y,
						])
						.attr("fill", "#ffe3cb")
						.attr("stroke-width", 10)
						.attr("stroke-linejoin", "round");
				}
			} else {
				pt.x += Math.random() * (2 + 2) - 1;
				pt.y += Math.random() * (2 + 2) - 1;
			}

			points.push([pt.x, pt.y]);
		}
		return points;
	}

	//mesh
	svg.append("g")
		.attr("fill", "none")
		.attr("stroke", "#ffe3cb")
		.selectAll("path")
		.data(circles)
		.join("polygon")
		.attr("points", (_d, i) => dupa(i))
		.attr("stroke-width", () => Math.random() * (20 - 10) + 10);

	return svg.node();
};

export default ThornicroftsGiraffe;
